@use '../util' as *;

.product {
  &__info-qty {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font: var(--text-preset-3);
    color: var(--clr-gray-950);
    background-color: var(--clr-gray-50);
    border: none;
    border-radius: rem(10);
    width: 100%;
    height: rem(56);

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
}

.input-wrapper {
  position: relative;
}
